import {Component, ElementRef, ViewChild} from '@angular/core';
import {Content, Events, IonicPage, LoadingController, ModalController, NavParams} from 'ionic-angular';
import {ModalComplete} from "../complete";
import {DptModel} from "../../../models/dpt-model";
import {DptProvider} from "../../../providers/departments/dpt-provider";

@IonicPage()
@Component({
  selector: 'page-chat',
  templateUrl: 'select-dpt.html',
})
export class SelectDpt {

  @ViewChild(Content) content: Content;
  @ViewChild('chat_input') messageInput: ElementRef;
  dpts: DptModel = [];
  private cellid: string;
  private bid: string;
    private uid: any;
    private phone: any;


  constructor(navParams: NavParams,
              private dpt: DptProvider,

            private events: Events,
              private loading: LoadingController,
              public modalCtrl: ModalController,) {
    this.cellid= navParams.get('data') || "None"
    ;
      this.uid = navParams.get('uid');
      this.phone = navParams.get('phone')
    this.bid= navParams.get('bid') || "1";
    console.log(this.cellid + "zz  " +this.bid)

  }

  ionViewWillLeave() {
    // unsubscribe
  }

  ionViewDidEnter() {
    //get notice list
    this.getDpt();

    // Subscribe to received  new notice events

  }




  async getDpt() {
    const loader = this.loading.create({
      content: "Getting data",
    });
    loader.present()
    this.dpt.query(this.bid
    ).then((res: any) => {
      this.dpts = JSON.parse(res.data);
      loader.dismissAll()
    });

  }



  openComplete(dpt){
    let modal = this.modalCtrl.create(ModalComplete,{
        dptid: dpt,
        cell:this.cellid,
        bid:this.bid,
        uid:this.uid,
        phone:this.phone});


    modal.present();
  }




}



